@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);


.login-container {
  display: grid; 
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
}

.part {
    height: 100vh;
    display: -webkit-flex;
    display: flex; 
    -webkit-align-items: center; 
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.alert-login {
    padding: 7px;
    font-size: 0.7rem;
    background-color: #FFBABA; /* Red */
    color: #D8000C;
    border: 1px solid darken(#FFBABA, 15%);
    margin-bottom: 10px;
  }

.login {
    background-color: black;
    color: #fffeee;
}

.sign-in-form {
    width: 200px;
    height: 250px;
}

.sign-in-form h1 {
    font-size: 1.5rem;
    margin: 20px 0;
}

.login-form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.login-form input {
    width: 200px;
    height: 30px;
    margin: 2px 0;
    border: 0;
    border-bottom: 1px solid gray;
    padding: 5px;
    color: white;
    background-color: black;
}
.login-form button {
    margin: 20px 0;
    width: 200px;
    height: 30px;
    border: 3px solid #fffeee;
    background-color: black;
    color: #fffeee;
    -webkit-align-self: center;
            align-self: center;
}

.sign-up {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 200px;
}

.sign-up span {
    font-size: 0.8rem;
    margin-right: 10px;
}

.sign-up button {
    width: 50px;
    font-size: 0.7rem;
    border: 0;
    border-bottom: 2px solid #fffeee;
    border-right: 2px solid transparent;
    background-color: black;
    color: #fffeee;
    transition: all 0.30s; 
    letter-spacing: none;
}

.sign-up button:hover{ 
    -webkit-transform : scaleX(1.1); 
            transform : scaleX(1.1);
    letter-spacing: 0.1px;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
    .sign-in-form h1 {
        font-size: 2.0rem;
    }
}

@media screen and (min-width: 375px) and (max-width: 736px) {
  .login-container {
    grid-template-rows: 1fr 1fr;
    grid-template-columns: auto;
  }
  .part {
      height: 50vh;
      -webkit-align-items: center;
              align-items: center;
  }
  .sign-in-form {
      height: 300px;
  }
}
